
@import url("https://fonts.googleapis.com/css?family=Open+Sans:400,500,600,700,800");
body, .header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
body {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-line-pack: center;
      align-content: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-family: 'Open Sans', sans-serif;
  margin: 1em 5em;
  text-align: left;
  word-break: break-word;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
a {
  border-bottom: 2px dotted;
  color: #2980b9;
  font-weight: 600;
  -webkit-transition-duration: 0.1s;
          transition-duration: 0.1s;
  text-decoration: none;
}
select {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: url(./assets/images/arrow.svg) no-repeat right 10px center;
  background-size: auto 60%;
  border: 1px solid #a7a8ab;
  border-radius: 5px;
  -webkit-box-shadow: 0 2px 5px 1px #F4F5F7;
          box-shadow: 0 2px 5px 1px #F4F5F7;
  display: block;
  font-size: 1em;
  padding: 5px 10px;
  margin-bottom: 1em;
  width: 100%;
}
.header {
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin-bottom: 2em;
}
.header h1 {
    font-size: 2.1em;
    font-weight: 700;
    margin: 0;
}
.header .capitol {
    border-radius: 8px;
    fill: #444;
    margin-bottom: 1em;
    width: 4em;
}
.error-status {
  background: #ffe7e8;
  border-radius: 8px;
  border: 1px solid;
  color: #da3333;
  list-style-type: none;
  margin-top: 2em;
  padding: 0.3em 1em;
}
.error-status .error-message {
    font-weight: bold;
}
.footer {
  margin-top: 3em;
}
.committee-facts {
  background: rgba(255, 251, 0, 0.22);
  border: 1px solid;
  border-radius: 4px;
  margin: 1.5em 0;
  padding: 1em 1.5em;
}
.committee-facts h2 {
    margin: 0 0 1rem;
    text-align: center;
}
.committee-facts p {
    margin: 0 0 0.4em;
}
.committee-facts .bold {
    font-weight: bold;
}
.committee-members {
  border-collapse: collapse;
  border-spacing: 1em;
  margin-bottom: 2em;
  width: 100%;
}
.committee-members .member-headers th {
    padding: 6px 4px;
}
.committee-members .member-info td {
    padding: 4px;
}
.committee-members .member-info:nth-child(even) {
    background: rgba(221, 221, 221, 0.4);
}
#app {
  color: #444;
  width: 40%;
}
#copy-button-container {
  text-align: center;
}
#copy-button-container .copy-button {
    background: -webkit-gradient(linear, left top, left bottom, from(#ea4335), to(#ea4d4d));
    background: linear-gradient(#ea4335, #ea4d4d);
    border: none;
    border-radius: 3px;
    border-style: none;
    color: #fff;
    cursor: pointer;
    font-family: inherit;
    font-size: 1em;
    font-weight: 600;
    padding: 0.5em 1em;
}
#copy-button-container .copy-button:focus {
      outline: 0;
}
#copy-button-container .copy-button:active, #copy-button-container .copy-button:hover {
      background: #e24242;
}



